

.dialog {
	position: fixed;
	top: 0;
	z-index: 10;
	width: 100%;
	height: 100%;
	display: none;
	color: #fff;
	background: transparent url('../img/shade.png') repeat;
}
.dialog > div {
	position: relative;
	display: block;
	margin: 20px auto;
	padding: 10px;
	width: 450px;
	font-size: 0.9em;
}
.dialog .close {
	position: relative;
	float: right;
	font-size: 1.2em;
}
.dialog h1 {
	margin: 0;
}
.dialog > div > div {
	margin: 5px auto;
}
.dialog a {
	color: #fff;
	font-weight: bold;
}
.dialog a:after {
	visibility: hidden;
	transform: scaleX(0);
}
.dialog a:hover {
	color: #85cdff;
}
.dialog.log > div > div {
	height: 400px;
	width: 100%;
	overflow: scroll;
}

.notices {
	height: auto;
	width: 100%;
	margin: 10px auto 20px;
	padding: 10px 0;
	text-align: center;
	border-top: 2px solid #999;
	border-bottom: 2px solid #999;
}
.notices .error {
    color: #990000;
}

.emphasis {
	height: auto;
	width: 100%;
	margin: 10px auto;
	padding: 10px 0;
	text-align: center;
	background-color: #fff;
	border: 1px solid #ddd;
	border-radius: 2px;
	box-shadow: 0 0 16px #eee inset;
}

@media only screen and (max-width: 600px) {
	
	.dialog > div {
		padding: 4%;
		width: 92%;
	}
}